




import { computed, defineComponent } from '@vue/composition-api'

export default defineComponent({
	name: 'DisplayHtml',
	props: {
		html: {
			type: String,
			required: true
		}
	},
	setup (props) {
		const value = computed(() => props.html?.replace(/<p><br><\/p>/gi, '') ?? '')
		return { value }
	}
})
